/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
@font-face {
  font-family: JustMandrone;
  src: url(/static/media/Just-Mandrawn.5a147d5f.ttf);
}

@font-face {
  font-family: PoppinsLight;
  src: url(/static/media/Poppins-Light.fcc40ae9.ttf);
}

@font-face {
  font-family: PoppinsRegular;
  src: url(/static/media/Poppins-Regular.093ee89b.ttf);
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@-webkit-keyframes reveal {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes reveal {
  0% {
    opacity: 0;
  }
  20% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.landing-page {
  background-image: url(/static/media/landing-page-background.19600518.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 100vh;
}
.landing-page .display-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 31px;
  gap: 31px;
}
.landing-page .content {
  padding-top: 30vh;
}
.landing-page .content .animation {
  -webkit-animation: reveal 4000ms;
          animation: reveal 4000ms;
  width: 100%;
}
.logo .text a {
  all: unset;
  font-size: 50px;
  font-family: JustMandrone, "Courier New", Courier, monospace;
}
.logo .text a:hover {
  cursor: pointer;
}
.results-count.hide {
  display: none;
}
.results-count .text {
  font-size: 16px;
  color: #6B515E;
  font-family: PoppinsRegular, "Courier New", Courier, monospace;
  font-weight: lighter;
}
.results-count .text .count {
  font-weight: bold;
}
.search {
  position: relative;
  width: calc(100% - 20px);
  max-width: 480px;
  padding-left: 10px;
  padding-right: 10px;
}
.search input {
  height: 64px;
  width: calc(100% - 87px - 16px - 2px);
  background-color: transparent;
  border: 1px solid #302B32;
  border-radius: 30px;
  padding-left: 87px;
  padding-right: 16px;
  font-size: 20px;
  font-family: PoppinsRegular, "Courier New", Courier, monospace;
  font-weight: bold;
  color: #6B515E;
}
.search .image {
  position: absolute;
  padding: 17.5px 14.5px 11.5px 17.5px;
  cursor: pointer;
  border-radius: 30px 0 0 30px;
}
.serp header {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 64px;
  gap: 64px;
  padding: 20px;
  background-color: #E8CBBD;
}
.serp__content {
  display: flex;
  flex-direction: row;
  grid-gap: 56px;
  gap: 56px;
  padding: 20px;
}
.serp__content__aside {
  border: 2px solid red;
  max-width: 280px;
  min-width: 280px;
}
.serp__content__results {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
}

@media screen and (max-width: 600px) {
  .serp header {
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
  }
  .serp__content {
    flex-direction: column;
  }
}
.job {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-family: PoppinsRegular, "Courier New", Courier, monospace;
}
.job .new {
  padding-left: 20px;
  padding-right: 27px;
}
.job .new.hide {
  visibility: hidden;
}
.job .description {
  border-style: solid;
  border-width: 1px;
  border-color: #C4C4C4;
  border-radius: 10px;
  padding-top: 20px;
  padding-left: 25px;
  padding-right: 25px;
  padding-bottom: 10px;
  width: 100%;
  max-width: 670px;
  min-width: 300px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.job .description .details {
  flex-grow: 2;
  max-width: 100%;
}
.job .description .details .company {
  font-size: 16px;
}
.job .description .details .section {
  display: flex;
  flex-direction: row;
}
.job .description .details .section .title {
  font-size: 18px;
  font-weight: 700;
  padding-top: 10px;
  padding-bottom: 10px;
}
.job .description .details .section .location {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  margin-left: 15px;
  color: #20A6FC;
}
.job .description .details .url {
  font-size: 14px;
  color: #71717A;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.job .description .type {
  flex-grow: 1;
  max-width: 150px;
}
.job .description .btn {
  all: unset;
  flex-grow: 1;
  max-width: 150px;
  min-width: 100px;
  height: 22px;
  line-height: 24px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 600;
  text-align: center;
  color: white;
  background-color: #20A6FC;
  border-radius: 100px;
}

@media screen and (max-width: 600px) {
  .job .new {
    display: none;
  }
  .job .description {
    flex-direction: column;
    grid-gap: 16px;
    gap: 16px;
    padding: 25px;
  }
  .job .description .details {
    flex-grow: 2;
  }
  .job .description .details .company {
    margin-bottom: 10px;
  }
}

